.experience{
    display: flex;
    align-items: center;
    width: 891px;
}

.experience-line{
    flex: 73%;
    height: 2px;
    background-color: #262626;
    margin: 0 6px;
}

.experience-icon{
    border: 2px solid #002200;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.experience-title{
    display: flex;
    margin: 6px 0 0 33px;
}

.experience-wrapper{
    display: flex;
    font-family: Inter, Arial, sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 17px;
    line-height: 18px;
}

.experience-subtitle{
    margin: 5px 0 0 96px;
    font-family: Inter, Arial, sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 18px;
}

.experience-links{
    margin-left: 93px;
}

.experience-link{
   margin-bottom: 5px;
}